<template>
	<div id="statcenter-keep">
		<public-header title="顾客留存统计"></public-header>
		<div class="content">
			<van-list class="border-0px">
				<van-cell-group class="border-0px">
					<van-cell class="no-fix" title="活跃顾客人数" :label="`最后一次成交在30天内的顾客，占比${stat.percent_active}%`" :value="`${stat.active}人`"></van-cell>
					<van-cell class="no-fix" title="沉默顾客人数" :label="`最后一次成交在30-60天内的顾客，占比${stat.percent_silence}%`" :value="`${stat.silence}人`"></van-cell>
					<van-cell class="no-fix" title="流失顾客人数" :label="`最后一次成交在60-60天内的顾客，占比${stat.percent_runoff}%`" :value="`${stat.runoff}人`"></van-cell>
					<van-cell class="no-fix" title="放弃顾客人数" :label="`最后一次成交在90天之前的顾客，占比${stat.percent_giveup}%`" :value="`${stat.giveup}人`"></van-cell>
				</van-cell-group>
			</van-list>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {
					active: 0,
					silence: 0,
					runoff: 0,
					giveup: 0,
					percent_active: 0,
					percent_silence: 0,
					percent_runoff: 0,
					percent_giveup: 0,
				},
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/statcenter/member/keep',
					data: {},
					autoAssign: true,
					variable: 'stat',
					success: () => {
						console.log(111, that.stat)
					}
				})
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>

</style>


